#root{
  width       : 100vw;
  height: 100%;
  display: inline-block;
  position: relative;
  justify-content: center;
  background-color: var(--color-background-primary);
  box-sizing      : border-box;
  font-family     : 'Inter',
    sans-serif;
}


@media screen and (max-width: 768px) {
  #root{
    width: 100%;
    margin: 0;
  }
}
